.App {
  min-height: 100vh;
  padding: 30px;
  display: flex;
  background: dimgray;
  justify-content: center;
  align-items: center;
}
.info{
  position: absolute;
  top:50px;
  font-size: 30px;
  font-weight: bold;
  color: cyan;
  text-transform: uppercase;
  text-shadow: 2px 2px 3px black;
  cursor: pointer;
}
.info:hover{
  color: olive;
  text-shadow: 2px 2px 3px cyan;
}
.App > div {
  margin-right: 50px;
}
.content  {
  display: flex;
  flex-wrap: wrap;
  width: 300px;
  min-height: 200px;
  border: 2px solid cyan;
  border-radius: 10px;
  justify-content: center;
  /*align-items: center;*/
  text-align: center;
  box-shadow: 2px 2px 3px black;
}
.goodValue{
  display: flex;
  flex-wrap: wrap;
  width: 90%;
  height: 150px;
  border: 2px solid cyan;
  border-radius: 10px;
  margin: 10px;
  align-items: center;
  justify-content: center;
  font-size: 25px;
  font-weight: bold;
  color: cyan;text-shadow: 2px 2px 3px black;
  box-shadow: 2px 2px 3px black;
}

.goodInc, .stop  {
  display: flex;
  width: 90%;
  height: 150px;
  border: 2px solid cyan;
  border-radius: 10px;
  margin: 10px;
  align-items: center;
  justify-content: center;
  font-size: 60px;
  font-weight: bold;
  text-shadow: 2px 2px 3px black;
  box-shadow: 2px 2px 3px black;
}
.goodInc {
  color: cyan;
}
.stop {
  color:red;
}
.message{
  font-size: 20px;
  color: cyan;
  text-shadow: 2px 2px 3px black;
}
.error{
  padding: 0 10px 5px;
  color:red;
  text-shadow: 2px 2px 3px white;
}
.correct{
  outline: 1px solid cyan;
  margin: 5px;
  font-size: 20px;
  width: 50px;
  border: 2px solid cyan;
  border-radius: 10px;
  font-weight: bold;
  text-align: center;
  color: black;
  box-shadow: 2px 2px 3px black;
}
.ignor{
  outline: 1px solid red;
  background: #ff8880;
  margin: 5px;
  font-size: 20px;
  width: 50px;
  border: 2px solid red;
  border-radius: 10px;
  font-weight: bold;
  text-align: center;
  box-shadow: 2px 2px 3px white;
}
.buttons {
  display: flex;
  width: 90%;
  border: 2px solid cyan;
  border-radius: 10px;
  margin: 5px;
  justify-content: center;
  box-shadow: 2px 2px 3px black;
}
.button {
  padding: 0 10px;
  font-size: 40px;
  font-weight: bold;
  background: cyan;
  margin: 10px;
  border: 2px solid cyan;
  border-radius: 10px;
  box-shadow: 2px 2px 3px black;
}
.button1 {
  padding: 0 10px;
  font-size: 40px;
  font-weight: bold;
  background: cyan;
  margin: 10px;
  border: 2px solid cyan;
  border-radius: 10px;
  box-shadow: 2px 2px 3px black;
}
.button1:hover{
  background: olive;
  border: 2px solid olive;
  box-shadow: 2px 2px 3px cyan;
  text-shadow: 2px 2px 3px cyan;
}
